* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition-duration: .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin: 0;
  padding: 24px 0 16px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.stars {
  align-items: end;
  padding-top: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__reviews {
  color: #060b35;
  margin-left: auto;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars__star:nth-child(5) {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
  padding: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link--is-active {
  color: #00acdc;
  position: relative;
}

.nav__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 3px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px;
  display: grid;
}

@media screen and (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

